import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'
const Home = resolve => require(['@/components/home/home.vue'], resolve);
const Search = resolve => require(['@/components/search/Search.vue'], resolve);
const LogoIn = resolve => require(['@/components/logoIn/logoIn.vue'], resolve);
const Register = resolve => require(['@/components/logoIn/register.vue'], resolve);
const Sms = resolve => require(['@/components/logoIn/SMSLogin.vue'], resolve);
const bachWord = resolve => require(['@/components/logoIn/retrievePassword.vue'], resolve);
const logoInBind = resolve => require(['@/components/logoIn/logoInBind.vue'], resolve);
const Notice = resolve => require(['@/components/page/Notice.vue'], resolve);
const theTeacher = resolve => require(['@/components/page/theTeacher.vue'], resolve);
const pickWeek = resolve => require(['@/components/page/pickweek.vue'], resolve);


Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        component: App, //顶层路由，对应index.html
        children: [ //二级路由。对应App.vue
            //地址为空时跳转home页面
            {
                path: '',
                redirect: { name: 'home', params: { userName: '请登录!' } }
            },
            //主页
            {
                path: '/home/:userName',
                name: 'home',
                component: Home
            },
            //登录
            {
                path: '/LogoIn',
                name: 'LogoIn',
                component: LogoIn
            },
            //短信验证登录
            {
                path: '/sms',
                name: 'sms',
                component: Sms
            },
            //注册
            {
                path: '/Register',
                name: 'register',
                component: Register
            },
            //找回密码
            {
                path: '/bachWord',
                name: 'bachWord',
                component: bachWord
            },
            //登录绑定
            {
                path: '/logoInBind',
                name: 'logoInBind',
                component: logoInBind
            },
            //爱股轩公告
            {
                path: '/Notice',
                name: 'Notice',
                component: Notice
            },
            //搜索页
            {
                path: '/search',
                name: 'search',
                component: Search
            },
            //名师专栏
            {
                path: '/theTeacher',
                name: 'theTeacher',
                component: theTeacher
            },
            //名师推荐商品
            {
                path: '/pickWeek',
                name: 'pickWeek',
                component: pickWeek
            }
        ]
    }]
});


