<template>
  <div>
    <transition name="bounce" mode="out-in">
    		<router-view></router-view>
    	</transition>
  </div>
</template>

<script>
export default {
  name: 'app'
}
</script>
<style lang="less" scoped>
 .bounce-enter-active {
  animation: bounce-in .5s;
}
.bounce-leave-active {
  animation: bounce-out .5s;
}
@keyframes bounce-in {
  0% {
    transform: scaleX(0);
  }
  50% {
    transform: scaleX(1.5);
  }
  100% {
    transform: scaleX(1);
  }
}
@keyframes bounce-out {
  0% {
    transform: scaleX(1);
  }
  50% {
    transform: scaleX(1.5);
  }
  100% {
    transform: scaleX(0);
  }
}
</style>



